export const textTruncate = (text = '', length = 100, ending = '...') => {
  if (!text || typeof text !== 'string') return ''; // Handle invalid inputs
  return text.length > length ? text.substring(0, length - ending.length) + ending : text;
};

export const match = (term, array = [], key) => {
  if (!term || !array.length || !key) return []; // Handle invalid inputs
  const reg = new RegExp(term.split('').join('.*'), 'i'); // Creates a fuzzy match regex
  return array.filter(item => item?.[key]?.match(reg)); // Uses optional chaining
};
